var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
let look=["所有人可见","仅自己可见"]
export default {
	data() {
		return {
			content:"娃哈哈",
			yins:"所有人可见",
			chushi:"娃哈哈",
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9]
		}
	},
	watch:{
		chushi(newval,oldval){
			console.log(newval)
		}
	},
	onLoad() {
		this.$nextTick(() => {
			this.$refs['showpopup'].open()
		})
	},
	onBackPress(){
		uni.showModal({
		    title: '提示',
		    content: '是否保持为草稿',
		    success: (res)=> {
		        if (res.confirm) {
		            console.log('用户点击确定');
		        } else if (res.cancel) {
		            console.log('用户点击取消');
		        }
		    }
		});
		return true
	},
	methods: {
		change(){
			console.log(11)
		},
		hide(){
			this.$refs['showpopup'].close()
		},
		detel(i){
			console.log(i)
			uni.showModal({
			    title: '提示',
			    content: '是否确认删除',
			    success:res=> {
			        if (res.confirm) {
						console.log(this.imageList)
						this.imageList.splice(i,1)
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		back(){
			console.log("back")
			if(!this.imageList.length && !this.chushi.length){
				return false
			}
			console.log(this.imageList+this.chushi)
			uni.showModal({
			    title: '提示',
			    content: '是否保持为草稿',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						uni.navigateBack({
							delta: 1
						});
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		backright(){
			console.log("发布")
		},
